*{
	box-sizing: border-box;
}

.progressbar {
	text-align: center;
	width:100%;
	zoom: 5;
}
.counter div {
	background-color: white;
	display: inline-block;
	border: 1px solid grey;
	border-radius: 50%;
	width: 2em;
	line-height: 1.88em;
	vertical-align: top;
}

.counter td {
	cursor: pointer;
	text-align: center;
	position: relative;
}

.counter span {
	position: absolute;
	top: 1em;
	left: -50%;
	width: 100%;
	border-top: 2px solid grey;
	z-index: -100;
}

.progressbar .active
,.progressbar .active *{
	border-color: red;
	color: red;
}

